// src/components/renderer/layouts/LayoutHero.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';

export const LayoutHero = ({ data, lang }: { data: SlideData; lang: Language }) => {
  const content = data.content[lang];

  // Renk haritası: Arka plan, metin ve vurgu renkleri
  const colorMap: Record<string, { bg: string, text: string, accent: string, blob: string }> = {
    blue: { bg: 'bg-blue-50', text: 'text-blue-900', accent: 'bg-blue-500', blob: 'bg-blue-300' },
    indigo: { bg: 'bg-indigo-50', text: 'text-indigo-900', accent: 'bg-indigo-500', blob: 'bg-purple-300' },
    purple: { bg: 'bg-purple-50', text: 'text-purple-900', accent: 'bg-purple-500', blob: 'bg-pink-300' },
    rose: { bg: 'bg-rose-50', text: 'text-rose-900', accent: 'bg-rose-500', blob: 'bg-orange-300' },
    emerald: { bg: 'bg-emerald-50', text: 'text-emerald-900', accent: 'bg-emerald-500', blob: 'bg-teal-300' },
    yellow: { bg: 'bg-yellow-50', text: 'text-yellow-900', accent: 'bg-yellow-500', blob: 'bg-orange-300' },
  };

  const theme = colorMap[data.themeColor] || colorMap.blue;

  return (
    <div className={`w-full h-full relative overflow-hidden flex flex-col items-center pt-24 ${theme.bg}`}>
      
      {/* Dekoratif Arkaplan Şekilleri (Blobs) */}
      <div className={`absolute -top-20 -left-20 w-96 h-96 rounded-full mix-blend-multiply filter blur-3xl opacity-40 ${theme.blob}`} />
      <div className={`absolute top-40 -right-20 w-80 h-80 rounded-full mix-blend-multiply filter blur-3xl opacity-40 ${data.themeColor === 'yellow' ? 'bg-yellow-300' : 'bg-indigo-200'}`} />

      {/* Metin Alanı */}
      <div className="z-20 text-center space-y-6 max-w-3xl px-8 relative">
        <div className="inline-block">
            <span className={`px-6 py-2 rounded-full text-xl font-bold shadow-sm uppercase tracking-wider bg-white ${theme.text} font-[var(--font-fredoka)]`}>
                {content.subtitle}
            </span>
        </div>
        <h1 className={`text-8xl font-[var(--font-luckiest)] ${theme.text} leading-tight drop-shadow-sm`}>
          {content.title}
        </h1>
      </div>

      {/* Telefon (3D) */}
      <div className="absolute inset-0 top-32 flex items-center justify-center perspective-1000 pointer-events-none">
         {/* perspective-1000 sınıfı 3D efekti için şart */}
         <div className="relative w-full h-full flex items-center justify-center" style={{ perspective: '1000px' }}>
            <PhoneMockup phone={data.phone} image={data.image} />
         </div>
      </div>
    </div>
  );
};