// src/components/renderer/layouts/LayoutSplit.tsx
import { SlideData, Language } from '@/core/types';
import { PhoneMockup } from '../PhoneMockup';

export const LayoutSplit = ({ data, lang }: { data: SlideData; lang: Language }) => {
  const content = data.content[lang];

  // Tema rengine göre gradyan arka plan
  const bgColors: Record<string, string> = {
    blue: 'from-blue-600 to-blue-900',
    indigo: 'from-indigo-600 to-indigo-900',
    purple: 'from-purple-600 to-purple-900',
    rose: 'from-rose-600 to-rose-900',
    emerald: 'from-emerald-600 to-emerald-900',
    yellow: 'from-yellow-500 to-orange-600',
  };
  const gradientClass = bgColors[data.themeColor] || 'from-slate-700 to-slate-900';

  return (
    <div className={`w-full h-full relative overflow-hidden flex bg-gradient-to-br ${gradientClass} text-white transition-colors duration-300`}>
        
      {/* Hafif Doku */}
      <div className="absolute inset-0 opacity-10 bg-[radial-gradient(circle_at_top_left,_var(--tw-gradient-stops))] from-white to-transparent" />

      {/* Sol taraf: Metin */}
      <div className="w-1/2 h-full flex flex-col justify-center p-16 z-20 relative">
        <h2 className="text-white/90 font-bold text-3xl mb-6 tracking-wide font-[var(--font-fredoka)] uppercase opacity-80">{content.subtitle}</h2>
        <h1 className="text-7xl font-[var(--font-luckiest)] mb-10 leading-tight drop-shadow-lg">{content.title}</h1>
        <div className="bg-white text-slate-900 px-10 py-5 rounded-2xl text-2xl font-black shadow-xl w-max font-[var(--font-fredoka)] transform transition hover:scale-105">
            {content.cta}
        </div>
      </div>

      {/* Sağ taraf: Telefon */}
      <div className="w-1/2 h-full relative flex items-center justify-center" style={{ perspective: '1200px' }}>
         {/* Arkadaki parlama efekti */}
         <div className="absolute w-[500px] h-[500px] bg-white/20 rounded-full blur-3xl" />
         
         <div className="relative w-full h-full">
             <PhoneMockup phone={data.phone} image={data.image} />
         </div>
      </div>
    </div>
  );
};